form {
  max-height: 100vh;
}

.filters {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    border: 3px solid #ffffff33;
    border-radius: 54px;
    font-size: 18px;
    color: #fffc;
}

.filter {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin: 10px 0;
}

.submitButtonWrapper {
    display: flex;
    justify-content: center;
}

button, input[type=submit] {
    background-color: transparent;
    cursor: pointer;
    text-transform: uppercase;
}

.submit-button {
  margin: 10px;
  /* font-family: 'Univers', sans-serif; */
  font-weight: 600;
  font-size: 20px;
  color: #fff;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: .25s;
  border: 3px solid;
  border-color: #e5e5e5;
  border-radius: 5px;
  padding: 7px 15px;
}

.submit-button:hover {
  color: #fbb10a;
  text-shadow: #fbb10a 0 0 10px;
  border-color: #fbb10a;
}

input[type=range] {
  width: 120px;
  cursor: pointer;
}

select {
  border-radius: 15px;
  padding: 15px 10px 0 10px;
  display:table-row;
  overflow: hidden;
  background-color: inherit;
  color: #fff;
}
option {
  border-radius: 5px;
  padding: 2px;
  display:table-cell;
}

.radio-wrapper {
  padding: 5px;
  display: flex;
  border-radius: 10px;
  background-color: #fff3;
}

.filter-switch-item {
  
  label {
    font-size: 14px;
    box-shadow: #fff3 0px 1px 3px 0px;
    margin: 2px;
    padding: 2px 4px 2px 4px;
    border-radius: 7px;
  }

  input {
    display: none;
  }

  input:checked + label {
    color: #fff;
    background-color: #3182ce;
  }

  input:not(:checked) + label {
    color: #fffc;
    --bg-opacity: 0;
    box-shadow: none;
  }
}

.filter-switch label {
  display: flex;
  cursor: pointer;
}


.filter-switch-item input:not(:checked) + label {
  color: #fffc;
  --bg-opacity: 0;
  box-shadow: none;
}

ul {
  list-style-type: none;
  margin: 0 auto;
}

.server {
  border: 3px solid #fff3;
  border-radius: 54px;
  margin-bottom: 10px;
  text-align: start;
  display: flex;
  align-items: flex-start;
}

.server .isloading {
  min-height: 108px;
  display: flex;
  align-items: center;
}

.server:hover {
  background-color: #ffffff08;
}
  
.server:hover .server-image {
  background-position: right center;
}

.server-image-wrapper {
  height: 102px;
  width: 102px;
  min-height: 102px;
  min-width: 102px;
  margin: 2px;
}

.server-image {
  border-radius: 50px;
  height: 100%;
  background-position: left center;
  background-repeat: no-repeat;
  background-size: cover;
  transition-property: all;
  transition-timing-function: cubic-bezier(.4,0,.2,1);
  transition-duration: 0.25s;
  text-align: center;
  /* will-change: transform; */
  display: flex; 
  align-items: center; 
  justify-content: center;
}

.server-info {
  margin: 10px 10px;
  width: 60%;
  max-width: 60%;
  /* font-size: calc(6px + 2vmin); */
}

.server-info-server-name {
  cursor: default;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 25px;
}

label {
  font-size: 18px;
  color: #fffc;
  text-transform: uppercase;
} 

hr {
  width: 150px;
  border: none;
  border-top: 1px dotted #fff3;
}

.server-info-server-params, .server-info-server-mods, .server-started {
  cursor: default;
  font-size: 18px;
  color: #fffc;
}

details>summary {
  cursor: pointer;
}

.server-mods-details-content {
  font-size: 16px;
  background: #000a;
  border: 2px solid #fffa;
  border-radius: 5px;
  margin: 10px;
  padding: 5px;
}

.server-utilization {
  cursor: default;
  margin: 10px 10px;
  width: 10%;
  font-size: 25px;
}

progress {
  max-height: 10px;
  width: 90%;
}

.server-location {
  cursor: default;
  margin: 10px 10px;
  width: 25%;
  font-size: 25px;
}

.server-flag-image {
  margin: 0 10px 0 0;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  /* max-width: 30px; */
}

.uppercase {
  text-transform: uppercase;
}